body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

img {
  width: 100%;
}

@import url("https://fonts.googleapis.com/css2?family=Cabin+Sketch:wght@400;700&family=Fredericka+the+Great&family=Homemade+Apple&display=swap");

@import url("https://fonts.googleapis.com/css2?family=Bellota+Text:ital,wght@0,300;0,400;0,700;1,300;1,400&display=swap");

body {
  background-color: rgba(0, 0, 0, 0.6);
  max-width: 100vw;
  font-family: "Bellota Text", cursive;
  background-image: url("./images/pizzaphoto.jpg");
  height: 100vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

@keyframes showTopText {
  0% {
    transform: translate3d(0, -100%, 0);
  }
  40%,
  60% {
    transform: translate3d(0, 50%, 0);
  }
  100% {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes showTopTextMobile {
  0% {
    transform: translate3d(0, -100%, 0);
  }
  100% {
    transform: translate3d(0, 0, 0);
  }
}

.animated-title {
  overflow: hidden;
}

.animated-title h1 {
  animation: showTopText 1s;
  animation-delay: 0.5s;
  animation-fill-mode: forwards;
  top: 0;
}

h1 {
  font-size: 5rem;
}

.App-header {
  font-family: "Cabin Sketch", cursive;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  text-align: center;
  height: 100vh;
}

.content {
  background-color: rgba(0, 0, 0, 0.85);
}

.introduction {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  line-height: 180%;

  margin: 0 10%;
  padding-top: 30px;
  text-align: justify;
  font-size: 1.1rem;
}

.menu {
  margin-top: 30px;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.menu p {
  margin-top: 30px;
}

.grid-max {
  display: inline-grid;
  width: 80%;
  margin: 0 20px;
  grid-template-columns: 50% 50%;
  grid-template-rows: 30px 30px 30px 30px;
}

.gridecske {
  font-size: 1.5rem;
  grid-row: 1;
  grid-column: 1;
}

.gride {
  font-style: italic;
  grid-row: 2;
  grid-column: 1;
}

.gridbee {
  font-size: 1.2rem;
  grid-row: 1;
  grid-column: 2;
  display: inline;
  text-align: right;
}

@import url("https://fonts.googleapis.com/css2?family=Lato:wght@700&display=swap");

.new-bookings {
  background-color: rgba(66, 67, 72, 0.8);
  color: whitesmoke;
  padding: 0.02rem 3rem;
  margin: auto;
  transform: translate(0px, 7.5px);
  width: fit-content;
  max-width: 95%;
  font-family: "Bellota Text", cursive;
}

.new-bookings :last-child {
  text-align: center;
  font-weight: 600;
}

.booking {
  text-align: center;
  font-size: 60px;
  font-weight: 900;
  font-family: "Cabin Sketch", cursive;
}

input,
textarea {
  display: block;
  width: 100%;
  margin-bottom: 15px;
  font-size: 20px;
  background-color: rgba(228, 228, 208, 0.85);
  text-align: end;
  line-height: 40px;
  border: 0;
  outline: none;
  border-radius: 7px;
  text-decoration: none;
}

label {
  font-size: 20px;
}

.new-booking button {
  font: inherit;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 1px solid rgb(103, 160, 113);
  background-color: rgb(103, 160, 113);
  color: white;
  margin: 1.6rem auto;
  border-radius: 7px;
  font-size: 1.2rem;
}

.btnDiv {
  margin: 40px 0;
  display: flex;
  justify-content: center;
}

button.new-booking {
  left: 50%;
  font: inherit;
  font-size: 1.5rem;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 1px solid rgb(103, 160, 113);
  background-color: rgb(103, 160, 113);
  color: white;
  margin: 2rem auto;
  border-radius: 7px;
}

button.new-booking:hover {
  box-shadow: rgba(103, 160, 113, 0.35) 0px 5px 15px;
}

.new-booking button:hover,
.new-booking button:active {
  background-color: #d8bf6c;
}
.new-booking input {
  text-align: center;
}

h2 {
  color: white;
  font-family: "Cabin Sketch", cursive;
  font-size: 2.5rem;
}

.pizza-triangle {
  background-image: url("./images/pizza-triangle.jpg");
  height: 100vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

div.new-booking {
  padding: 0;
}

.App-header h1 {
  width: 50vw;
}

@media screen and (max-width: 500px) {
  h1 {
    font-size: 2rem;
    margin-top: 19px;
  }

  .App-header h1 {
    width: 80vw;
    line-height: 130%;
  }

  .App-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 25vh;
    background-color: rgba(66, 67, 72, 0.8);
    border-bottom: dashed 2px white;
  }

  .introduction {
    margin: 0 5%;
  }

  .content {
    background-color: rgba(66, 67, 72, 0.8);
  }

  .content h2 {
    margin-top: 10px;
    font-size: 2rem;
  }

  .grid-max {
    display: inline-grid;
    width: 90%;
    margin: 0 5px;
    grid-template-columns: 100%;
    grid-template-rows: 30px 30px 30px 30px;
  }

  .gridecske {
    font-size: 1.5rem;
    grid-row: 1;
    grid-column: 1;
  }

  .gride {
    font-style: italic;
    grid-row: 2;
    grid-column: 1;
  }

  .gridbee {
    font-size: 1.2rem;
    grid-row: 1;
    grid-column: 1;
    display: inline;
    text-align: right;
  }

  .new-bookings {
    height: 100vh;
    transform: translate(0px, 0px);
  }

  .pizza-triangle {
    overflow: hidden;
  }

  .pizza-triangle button {
    margin-top: 60px;
  }

  body {
    background-image: url("./images/pizza-triangle.jpg");
  }

  .animated-title {
    overflow: hidden;
  }

  .animated-title h1 {
    animation: showTopTextMobile 1s;
    animation-delay: 0.5s;
    animation-fill-mode: forwards;
    top: 0;
  }
}
